import {navigate} from "@reach/router"
import {GatsbyBrowser} from "gatsby"
import React from "react"
import Layout from "./src/components/Layout"
import {AuthProvider} from "./src/contexts/AuthContext"
import {I18nProvider} from "./src/contexts/I18nContext"
import {NavBarContextProvider} from "./src/contexts/NavBarContext"
import {TransitionContextProvider} from "./src/contexts/TransitionContext"

// export const onClientEntry: GatsbyBrowser["onClientEntry"] = (args, options) => {}

// export const onInitialClientRender: GatsbyBrowser["onInitialClientRender"] = () => {}

export const onPreRouteUpdate: GatsbyBrowser["onPreRouteUpdate"] = ({location, prevLocation, ...otherProps}) => {
    if (location.pathname === "/") {
        const acceptLanguageHeader = navigator.language
        const acceptedLanguages = acceptLanguageHeader.split(",")
        const preferredLanguage = acceptedLanguages[0]

        const DEFAULT_LANGUAGE = `${process.env.DEFAULT_LANGUAGE || "it"}`
        const SUPPORTED_LANGUAGES = `${process.env.SUPPORTED_LANGUAGES || DEFAULT_LANGUAGE}`.split(",")
        const browserLanguage = navigator.language.substring(0, 2)
        if (SUPPORTED_LANGUAGES.indexOf(browserLanguage.toLowerCase()) >= 0) {
            window.location.replace(`${process.env.PATH_PREFIX}/${browserLanguage}`)
        } else {
            window.location.replace(`${process.env.PATH_PREFIX}/${DEFAULT_LANGUAGE}`)
        }

        navigate(`/${preferredLanguage}`)
    }
}

// export const onRouteUpdate: GatsbyBrowser["onRouteUpdate"] = ({location, prevLocation, ...otherProps}) => {}

export const wrapPageElement: GatsbyBrowser["wrapPageElement"] = ({element, props}) => {
    if (props.location.pathname === `${process.env.PATH_PREFIX}/`) return <>{element}</>
    return (
        <I18nProvider>
            <TransitionContextProvider reachLocation={props.location}>
                <AuthProvider>
                    <NavBarContextProvider>
                        <Layout {...props}>{element}</Layout>
                    </NavBarContextProvider>
                </AuthProvider>
            </TransitionContextProvider>
        </I18nProvider>
    )
}

